import Link from "next/link";
import { useState, useEffect } from "react";

const productList = Array.from({ length: 100 }).map((_, i) => ({
  id: i + 1,
  name: `Product ${i + 1}`,
  image: `/clothes/${(i % 6) + 1}.jpg`,
}));

export default function ClothesGrid() {
  const [visibleCount, setVisibleCount] = useState(16);

  useEffect(() => {
    const onScroll = () => {
      if (window.innerHeight + window.scrollY >= document.body.offsetHeight - 200) {
        setVisibleCount((prev) => Math.min(prev + 8, productList.length));
      }
    };
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <div style={{
      display: "grid",
      gridTemplateColumns: "repeat(4, minmax(0, 1fr))",
      gap: 32,
      maxWidth: 1200,
      margin: "40px auto"
    }}>
      {productList.slice(0, visibleCount).map(product => (
        <Link key={product.id} href={`/product/${product.id}`} style={{ textDecoration: "none" }}>
          <div
            style={{
              background: "#eee",
              borderRadius: 24,
              padding: 0,
              boxShadow: "0 2px 12px #bbb",
              transition: "transform 0.2s, box-shadow 0.2s",
              cursor: "pointer"
            }}
            onMouseEnter={e => {
              (e.currentTarget as HTMLDivElement).style.transform = "scale(1.04)";
              (e.currentTarget as HTMLDivElement).style.boxShadow = "0 8px 32px #b71c1c";
            }}
            onMouseLeave={e => {
              (e.currentTarget as HTMLDivElement).style.transform = "scale(1)";
              (e.currentTarget as HTMLDivElement).style.boxShadow = "0 2px 12px #bbb";
            }}
          >
            <img
              src={product.image}
              alt={product.name}
              style={{ width: "100%", height: 250, objectFit: "cover", borderRadius: 24, marginBottom: 0 }}
            />
            <div style={{ padding: "18px 0", textAlign: "center", fontWeight: 600, fontSize: 20, color: "#222" }}>{product.name}</div>
          </div>
        </Link>
      ))}
    </div>
  );
}
